import * as THREE from "three";
import LocomotiveScroll from "locomotive-scroll";
import imagesLoaded from "imagesloaded";
import gsap from "gsap";
import {loadPage} from "./animatePage"
import vertexShader from "../shaders/vertex.glsl";
import fragmentShader from "../shaders/fragment.glsl";

export default class Sketch {
  constructor(options) {
    this.time = new THREE.Clock();;
    this.container = options.dom;
    this.scene = new THREE.Scene();

    this.width = this.container.offsetWidth;
    this.height = this.container.offsetHeight;

    this.camera = new THREE.PerspectiveCamera(
      2 * Math.atan(this.height / 2 / 600) * (180 / Math.PI),
      this.width / this.height,
      100,
      2000
    );
    this.camera.position.z = 600;

    // this.camera.fov = 2 * Math.atan((this.height / 2 / 400) * (180 / Math.PI));

    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
    });
    this.renderer.setSize(this.width, this.height);
    this.container.appendChild(this.renderer.domElement);

    /**
     * Plane Materials
     */
    this.planeMaterial = new THREE.ShaderMaterial({
      fragmentShader,
      vertexShader,
      uniforms: {
        uImage: { value: 0 },
        uScrollSpeed: { value: 0 },
        uResolution: { value: new THREE.Vector2(0, 0) },
        uEnter: { value: 0 },
        uTime: { value: 0 },
      },
    });

    /**
     * Add Images
     */
    imagesLoaded(document.querySelector("img"), () => {
      this.addImages();
      this.render();
      this.setImagePosition();
      this.initScroll();
      this.setupResize();
      this.scroll.update()
      loadPage()
    });
  }

  /**
   * Scroll
   */
  initScroll() {
    this.scroll = new LocomotiveScroll({
      el: document.querySelector("[data-scroll-container]"),
      smooth: true,
      smartphone: {
        smooth: true,
      },
      getSpeed: true,
    });
    this.scrollPosition = { x: 0, y: 0 };
    this.scrollSpeed = 0;
    this.scroll.on("scroll", (args) => {
      this.scrollPosition = args.scroll;
      this.scrollSpeed = args.speed;
      this.setImagePosition();
    });
  }

  addImages() {
    const images = document.querySelectorAll("img[data-warp]");
    this.imageStore = [];

    for (const image of images) {
      const { width, height, top, right, left, bottom } =
      image.getBoundingClientRect();
      const geometry = new THREE.PlaneGeometry(1, 1, 1);
      const material = this.planeMaterial.clone();

      let texture = new THREE.Texture(image);
      texture.needsUpdate = true;

      material.uniforms.uImage.value = texture;
      material.uniforms.uResolution.value.x = width;
      material.uniforms.uResolution.value.y = height;
      if (image.getAttribute("data-warp") == "static") {
        let tl = gsap.timeline()
        image.addEventListener("mouseenter", () => {
          material.uniforms.uTime.value = this.time.getElapsedTime()
          tl.to(material.uniforms.uEnter, { value: 1, duration: .05 })
            .to(material.uniforms.uEnter, { value: 0, duration: .05 })
        });
        image.addEventListener("mouseleave", () => {
            material.uniforms.uTime.value = this.time.getElapsedTime()
            tl.to(material.uniforms.uEnter, { value: 1, duration: .05 })
              .to(material.uniforms.uEnter, { value: 0, duration: .05 })
        })
      }

      const mesh = new THREE.Mesh(geometry, material);
      this.scene.add(mesh);

      image.classList.toggle("hide-image");

      this.imageStore[image.id] = {
        id: image.id,
        image,
        mesh,
        originalWidth: width,
        originalHeight: height,
        width,
        height,
        top,
        right,
        left,
        bottom,
      }
    }
  }

  setImagePosition() {
    const inviewImages = document.querySelectorAll("*[data-scroll-section-inview] img[data-warp]")
    inviewImages.forEach((imageNode) => {
      const { width, height, top, left } = this.imageStore[imageNode.id].image.getBoundingClientRect();
      this.imageStore[imageNode.id].width = width;
      this.imageStore[imageNode.id].height = height;
      this.imageStore[imageNode.id].top = top;
      this.imageStore[imageNode.id].left = left;
  
      this.imageStore[imageNode.id].mesh.material.uniforms.uScrollSpeed.value = this.scrollSpeed;
      this.imageStore[imageNode.id].mesh.position.x = this.imageStore[imageNode.id].left - this.width / 2 + this.imageStore[imageNode.id].width / 2;
      this.imageStore[imageNode.id].mesh.position.y = -this.imageStore[imageNode.id].top + this.height / 2 - this.imageStore[imageNode.id].height / 2;
      this.imageStore[imageNode.id].mesh.scale.set(this.imageStore[imageNode.id].width, this.imageStore[imageNode.id].height);
    })
  }

  /**
   * Resizing
   */
  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
    this.setImagePosition();
  }
  resize() {
    this.width = this.container.offsetWidth;
    this.height = this.container.offsetHeight;
    this.renderer.setSize(this.width, this.height);
    this.camera.aspect = this.width / this.height;
    this.camera.updateProjectionMatrix();
  }

  render() {
    this.renderer.render(this.scene, this.camera);

    window.requestAnimationFrame(this.render.bind(this));
  }
}
